@import url("https://fonts.googleapis.com/css?family=Roboto:400,500,700");
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.container {
  width: 1178px;
  margin: 0px auto;
  position: relative;
  padding-left: 6px;
}

.container--min {
  width: 93%;
}

.about_us {
  background-color: #fff;
  padding-bottom: 98px;
}

.mailing {
  background: url(../img/bg.png) no-repeat center center/cover;
  padding-top: 143px;
  padding-bottom: 150px;
}

@media only screen and (max-width: 1224px) {
  .mailing {
    width: 1224px;
  }
}

div, p, input, button, a {
  box-sizing: border-box;
}

hr {
  height: 2px;
  margin-top: 64px;
  margin-bottom: 42px;
  color: #d8d8d8;
}

@media only screen and (max-width: 1224px) {
  hr {
    width: 1224px;
  }
}

body {
  width: 100%;
  overflow-x: hidden;
  font-family: 'Roboto', Arial, sans-serif;
}

header {
  height: 894px;
  background: url(../img/bg-poly2.png) no-repeat center center/cover;
}

@media only screen and (max-width: 1224px) {
  header {
    width: 1224px;
  }
}

nav {
  float: right;
}

footer {
  padding-bottom: 58px;
}

.heading__logo {
  float: left;
  margin-top: 29px;
}

.heading__menu {
  margin-top: 59px;
  margin-left: 377px;
  display: block;
}

.heading__item {
  float: left;
  display: block;
  margin-right: 61px;
  color: #fff;
  font-size: 18px;
  font-weight: 400;
}

.heading__item:last-child {
  margin-right: 0px;
}

.heading__item a {
  color: #fff;
  text-decoration: none;
}

.button {
  margin-top: 92px;
  background: none;
  border: 2px solid #fff;
  border-radius: 5px;
  padding: 20px 59px;
  text-transform: uppercase;
  color: #fff;
  font-size: 18px;
  font-weight: 400;
}

.button:hover {
  color: #fc6744;
  background: #fff;
}

.button:focus {
  outline: none;
}

.button--white {
  padding: 0;
  width: 195px;
  margin-left: 15px;
  height: 63px;
  background: #fff;
  color: #fe8237;
}

.button--white:hover {
  background: #fe8237;
  color: #fff;
}

.h2--black {
  font-size: 38px;
  font-weight: 600;
}

.h2--orange {
  font-size: 36px;
  font-weight: 800;
  margin-top: 18px;
}

.h2--upper {
  letter-spacing: 0.2em;
  font-size: 30px;
  text-transform: uppercase;
  font-weight: 800;
  color: #fff;
}

.mainTxt__title {
  float: left;
  width: 570px;
  margin-top: 235px;
  color: #fff;
}

.mainTxt__p {
  font-size: 20px;
  font-weight: 400;
  margin-top: 36px;
  line-height: 1.8;
}

.mainImg {
  float: right;
  margin-top: 125px;
}

.mainImg__sign {
  position: absolute;
  margin-top: 320px;
  margin-left: 105px;
  font-size: 18px;
  font-weight: 400;
  color: #fff;
}

.article__title {
  margin-top: 93px;
  text-align: center;
}

.article__p {
  color: #fc6744;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 400;
}

.grid {
  display: grid;
  justify-content: center;
  padding-top: 75px;
  grid-template-columns: repeat(auto-fill, 350px);
  grid-gap: 60px 58px;
}

.egg__img {
  text-align: center;
}

.egg__title {
  margin-top: 45px;
  font-weight: 800;
  font-size: 18px;
  text-align: center;
}

.egg__p {
  margin-top: 12px;
  font-weight: 400;
  font-size: 18px;
  text-align: center;
  line-height: 1.6;
}

.whitetxt__title {
  margin: 0 auto;
  margin-top: 3px;
  width: 800px;
  text-align: center;
  color: #fff;
}

.whitetxt__p {
  width: 760px;
  margin-top: 15px;
  line-height: 2;
  font-size: 18px;
  font-weight: 600;
}

.mainForm {
  text-align: center;
}

.mainForm__mail {
  font-weight: 400;
  font-size: 16px;
  width: 603.5px;
  margin-right: 10px;
  height: 63px;
  border: 2px solid #fff;
  border-radius: 5px;
  background: none;
  padding: 0 30px;
  color: #fff;
}

.mainForm__mail:focus {
  outline: none;
}

.footer__copy {
  float: left;
  font-size: 16px;
  color: #878787;
}

.social {
  float: right;
}

.social__icon {
  margin-left: 37px;
}
